<template>
  <div>
    <v-dialog persistent v-model="show" max-width="500px" hide-overlay>
      <v-card>
        <v-card-title>
          <span class="text-h5">Edit Product</span>
        </v-card-title>

        <v-card-text>
          <v-container>
            <v-form
              @submit.prevent="save"
              ref="form"
              v-model="valid"
              lazy-validation
            >
              <v-row>
                <v-col class="py-0" cols="12" sm="6" md="12">
                  <v-text-field
                    dense
                    label="Title"
                    v-model="title"
                    :rules="[(v) => !!v || $t('fieldRequired')]"
                    outlined
                  ></v-text-field>
                </v-col>

                <v-col class="py-0" cols="12" sm="6" md="6">
                  <v-select
                    dense
                    :items="categories"
                    :rules="[(v) => !!v || $t('fieldRequired')]"
                    label="Category"
                    outlined
                  ></v-select>
                </v-col>
                <v-col class="py-0" cols="12" sm="6" md="6">
                  <v-text-field
                    dense
                    type="number"
                    label="Price"
                    :rules="[(v) => !!v || $t('fieldRequired')]"
                    outlined
                  ></v-text-field>
                </v-col>
                <v-col class="py-0" cols="12" sm="6" md="12">
                  <v-textarea
                    outlined
                    v-model="description"
                    label="Descriptiona"
                    :rules="[(v) => !!v || $t('fieldRequired')]"
                  ></v-textarea>
                </v-col>
              </v-row>
            </v-form>
          </v-container>
        </v-card-text>
        {{ $props.productEdit.description }}
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="blue darken-1" text @click="closeDialog()">
            Cancel
          </v-btn>
          <v-btn color="blue darken-1" text :loading="loading" @click="save()">
            Save
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import { mapActions, mapState } from "vuex";
export default {
  props: ["showDialog", "productEdit"],
  emits: ["colseDialog"],
  data() {
    return {
      loading: false,

      valid: true,
      product: this.productEdit,
      // show: this.showDialog,
      // title: this.productEdit.title,
      description: this.productEdit.description,
    };
  },
  computed: {
    ...mapState("core", ["categories"]),
    show() {
      return this.showDialog;
    },

    id() {
      return this.productEdit.id;
    },
  },
  methods: {
    ...mapActions("core", ["getCategories", "editProduct"]),
    closeDialog() {
      this.$emit("colseDialog");
    },
    save() {
      const validForm = this.$refs.form.validate();
      if (validForm) {
        this.loading = true;
        this.editProduct({ id: this.id, product: this.product });
        this.$emit("colseDialog");
        this.loading = false;
        this.$toast.info("Edit product successfully");
      }
    },
  },
  mounted() {
    console.log(this.productEdit.description, ".....");
    this.getCategories();
  },
};
</script>
